.header {
  width: 100%;
  height: 100vh;
  background-color: $main-color;
  background-image: url(../img/girlmobile.jpg);
  background-repeat: no-repeat;
  background-position-x: center;
  background-position-y: bottom;
  background-size: cover;
}


.header__container {
  /*  max-width: 1440px;*/
  width: 100%;
  background-color: transparent;
  position: fixed;
  margin: 0 auto;
  left: 0;
  right: 0;
  padding: 0 20px;
  box-sizing: border-box;
  display: flex;
  justify-content: space-between;
  align-items: center;
  transition: 0.3s all ease-out;
  z-index: 8;

  .wrapper {
    width: 100%;
  }

  &--services {
    .wrapper {
      max-width: 1300px;
    }
  }
}

.header__contact-container {
  margin-bottom: 40px;
}

.header__nav {
  max-width: 320px;
  width: 100%;
  height: 46px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-weight: bold;

  a {
    font-size: 14px;
    text-transform: uppercase;
    text-decoration: none;
    color: #222222;
    line-height: 45px;
    position: relative;
    z-index: 10;
    cursor: pointer;

  }
}

.header__main-nav {
  width: 100%;
  display: flex;
  position: fixed;
  flex-direction: column;
  justify-content: space-between;
  box-sizing: border-box;
  top: 0;
  height: 100%;
  padding: 41px 20px 0 20px;
  left: 0;
  z-index: 9;
  transform: scaleY(0);
  background-color: #ffffff;
  transform-origin: top;
  transition: transform .5s cubic-bezier(.28, 0, .18, 1) .5s;

  overflow-y: auto;

  &--active {
    transform-origin: top;
    transform: scaleY(1);

    .header__nav-elements,
    .header__contact-container {
      opacity: 1;
      transition: opacity 1.5s cubic-bezier(.28, 0, .18, 1) .5s;
    }
  }
}

.header__contact-container,
.header__nav-elements {
  transition: opacity 0.1s cubic-bezier(.28, 0, .18, 1) .3s;
  opacity: 0;
  padding: 0;
  list-style: none;
}

.header__nav-elements a:hover {
  border-bottom: 1px solid #222222;
}

a.header__tel {
  font-size: 12px;
}

.header__logo {
  display: flex;
  align-items: center;
  margin-left: 2px;
  z-index: 10;
}

.header__burger {
  cursor: pointer;
  position: relative;
  width: 30px;
  height: 30px;
  z-index: 10;
}

.header__burger-ico {
  position: absolute;
  top: 14px;
  display: block;
  width: 26px;
  height: 1px;
  background-color: #222222;
  transition: all .6s ease;

  &::before {
    content: "";
    position: absolute;
    top: -5px;
    display: block;
    width: 26px;
    height: 1px;
    background-color: #222222;
    transition: .6s cubic-bezier(.47, -.77, .6, 1.8);
  }

  &::after {
    content: "";
    position: absolute;
    top: 5px;
    display: block;
    width: 26px;
    height: 1px;
    background-color: #222222;
    transition: .6s cubic-bezier(.47, -.77, .6, 1.8);
  }

  &--active {
    &::after,
    &::before {
      top: 0;
    }
  }
}

.header__info-social {
  display: none;
  z-index: 1;
}

.header__info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 77vh;
  padding: 0 20px;
  min-height: 254px;

  h1 {
    font-size: 21px;
    line-height: 24px;
    font-weight: 900;
    margin-bottom: 0;
  }

  p {
    line-height: 30px;
    font-weight: 500;
    margin-bottom: 20px;
    margin-top: 0;
  }
}

.header__button {
  margin-bottom: 60px;

  &:hover {
    background-color: #f8edc6;
  }
}

.header__decor {
  display: none;
}

.header__service-items {
  list-style: none;
  max-width: 1042px;
}

.header__services--active {
  border-bottom: 1px solid #222;
}

a.header__service-link:hover {
  border: none;
}

.header__service-link {
  &:hover {
    text-decoration: underline;
    text-underline-color: #222222;
  }

  br,
  span {
    display: none;
  }
}
@media (min-width: 500px) {
  .header {
    background-size: 113%;
    background-image: url(../img/girl740px.jpg);
    background-position-x: 13vh;
    height: 100vh;
  }
}

@media (max-width: 900px) {
  .header__services-wrapper {
    overflow: hidden;
    height: 0;
    transition: 0.4s ease-in-out height;
  }
  .header__services:hover + .header__services-wrapper,
  .header__services:focus + .header__services-wrapper {
    height: 180px;
  }
}

@media (min-width: 900px) {
  .header {
    background-size: auto;
    background-image: url(../img/girl740px.jpg);
    background-position-x: 36vh;
    height: 100vh;
  }

  .header__info {
    height: 100vw;
  }

  .header__decor {
    position: absolute;
    display: block;
  }

  .header__decor-img {
    position: absolute;

    &--1 {
      top: 1vh;
      left: 20vw;
    }

    &--2 {
      top: 22vh;
      left: -1vw;
      transform: scale(1.4);
    }

    &--3 {
      top: 66vh;
      left: 9vw;
    }

    &--4 {
      top: 27vh;
      left: 46vw;
    }

    &--5 {
      top: 10vh;
      left: 570px;
    }

    &--6 {
      top: 16vh;
      left: 163px;
    }

    &--7 {
      top: 78vh;
      left: 25vw;
    }

    &--8 {
      top: 35vh;
      left: 56vw;
      transform: scale(0.9);
    }

    &--9 {
      top: 11vh;
      left: 54vw;

    }

    &--10 {
      top: 27vh;
      left: 92vw;
    }

    &--11 {
      top: 97vh;
      left: 9vw;
    }
  }

  .header__container {
    padding: 0 20px;
  }

  .header__burger {
    display: none
  }

  .header__nav {
    max-width: 100%;
    height: auto;
  }

  .header__main-nav {
    position: static;
    align-items: flex-end;
    padding: 0;
    transform: none;
    background-color: transparent;
  }

  .header__nav-elements {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    width: 100%;
    opacity: 1;

    li:not(:last-of-type) {
      margin-right: 30px;
    }
  }

  .header__contact-container {
    opacity: 1;
    margin-bottom: 0;
    font-weight: normal;
    margin-top: 0;
    margin-left: 130px;
    min-width: 162px;

    .social {
      display: none;
    }
  }

  .header__logo img {
    height: 37px;
    width: 92px;
  }

  .header__button {
    margin-bottom: 0;
  }

  .header__info-container {
    overflow: hidden;
    height: 100vh;
    display: flex;
    justify-content: space-between;
    padding: 0 20px;

  }

  .header__info {
    height: 95vh;
    justify-content: center;
    align-items: flex-start;
    padding: 0;
    z-index: 2;

    h1 {
      font-size: 27px;
      line-height: 28px;
    }
  }

  .header__info-social {
    display: flex;
    margin-bottom: 5vh;
    align-self: flex-end;
  }


  .header__service-bg {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    z-index: 0;
    background-color: rgba(0, 0, 0, 0.8);
    visibility: hidden;
    opacity: 0;
    transition: opacity 0.3s ease 0.4s;
  }
  a.header__tel {
    font-weight: 700;
    font-size: 15px;
  }
  .header__service-items-wrapper {
    background-color: white;
    width: 100%;
    padding: 92px 20px 20px 20px;
    position: fixed;
    top: 0;
    left: 0;
    transform: translateY(-100%);
    transition: 0.3s transform ease-in 0.3s;
    z-index: 1;
  }

  .header__service-items {
    display: flex;
    justify-content: space-between;
    margin: 0 auto;
    list-style: none;
    padding-left: 0;

    a {
      font-size: 14px;
      line-height: 20px;
      font-weight: 500;
      text-transform: capitalize;
    }

    li:not(:last-of-type) {
      margin-right: 0;
    }
  }

  .header__main-container {
    display: flex;
    align-items: center;
    width: 100%;

  }

  .header__service-item {
    text-align: center;
    background-color: #ffdd58;
    padding-top: 20px;
    padding-bottom: 20px;
    width: 24%;
    height: 120px;
    max-width: 238px;
    max-height: 154px;
    display: flex;
    align-items: flex-end;
    opacity: 0;
    transition: 0.4s all ease-in;
  }
  a.header__service-link {
    margin: 0 auto;
    position: relative;

    br {
      display: block;
    }

    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 50%;
      width: 38px;
      height: 35px;
      transform: translate(-50%, -120%);
      background-repeat: no-repeat;
      background-size: cover;
    }

    &--eng::before {
      background-image: url("../img/great-britain.svg");
    }

    &--nem::before {
      background-image: url("../img/germany.svg");
    }

    &--pol::before {
      background-image: url("../img/poland.svg");
    }

    &--fr::before {
      background-image: url("../img/france.svg");
    }

    &--active {
      text-decoration: underline;
    }
  }

  .header__service-items-wrapper:focus-within + .header__service-bg,
  .header__service-items-wrapper:hover + .header__service-bg,
  .header__services:focus-within + .header__services-wrapper > .header__service-bg,
  .header__services:hover + .header__services-wrapper > .header__service-bg {
    visibility: visible;
    opacity: 1;
    transition: opacity 0.3s ease-in;
  }
  .header__service-items-wrapper:hover,
  .header__service-items-wrapper:focus-within,
  .header__services:focus-within + .header__services-wrapper > .header__service-items-wrapper,
  .header__services:hover + .header__services-wrapper > .header__service-items-wrapper {
    transform: translateY(0);
    transition: 0.3s transform ease-out 0.2s;

  }

  .header__service-items-wrapper:hover .header__service-item,
  .header__service-items-wrapper:focus-within .header__service-item,
  .header__services:focus-within + .header__services-wrapper > .header__service-item,
  .header__services:hover + .header__services-wrapper .header__service-item {
    opacity: 1;
    transition: 0.4s opacity ease-in 0.4s;
  }
}

@media (min-width: 920px) {
  .header__container {
    padding: 0 50px;
  }
  .header__decor-img {
    &--4 {
      top: 92vh;
      left: 90vw;
    }

    &--5 {
      top: 19vh;
      left: 295px;
    }

    &--8 {
      top: 35vh;
      left: 75vw;
    }
  }

  .header__info-container {
    padding: 92px 50px 50px 50px;

  }


  .header__main-nav {
    padding: 0px 50px 0 50px;

  }
}

@media (min-width: 992px) {
  .header {
    background-position-x: -22vh;
    background-image: url(../img/girl.jpeg);
  }
  .header__decor-img {
    &--8 {
      display: none;
    }
  }
  @media (-webkit-min-device-pixel-ratio: 1.5), (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
    .header {
      background-position-x: -22vh;
      background-image: url(../img/girlforretina.jpeg);
      background-size: 1440px auto;
    }
  }
}

@media (min-width: 1200px) {
  .header__main-container {
    flex-direction: row;
    align-items: center;
    width: 100%;
  }
  .header__service-item {
    width: 24%;
    height: 154px;
    align-items: center;
  }
  a.header__service-link {
    font-size: 16px;

    transform: translateY(50%);

    span {
      display: inline;
    }

    br {
      display: none;
    }
  }

  .header__service-items-wrapper {
    padding: 92px 70px 50px 70px;
  }

  .header {
    background-position-x: -22vh;
    height: 95vh;
  }

  .header__info-social {
    margin-top: -40px;
    justify-content: flex-end;

  }

  .header__container {
    padding: 0 70px;
  }

  .header__main-nav {
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
  }

  .header__nav-elements {
    width: auto;
    margin-left: auto;
  }

  .header__contact-container {
    margin-top: 0;
    margin-left: 130px;
  }

  a.header__tel {
    font-weight: bold;
    font-size: 15px;
  }

  .header__info {
    padding-left: 200px;

    h1 {
      font-size: 36px;
      line-height: 48px;
    }

    p {
      font-size: 18px;
      margin-bottom: 40px;
      margin-top: 10px;
    }
  }

  .header__info-container {
    position: relative;
    height: 95vh;
    padding: 0 70px 0 0;
  }

}

@media (min-width: 1440px) {
  .header {
    background-position-x: 92%;
  }

  .header__info {
    padding-left: 200px;
  }

  .header__decor-img {
    position: absolute;

    &--1 {
      top: 2vh;
      left: 29vw;
    }

    &--2 {
      top: 24vh;
      left: 20px;
      transform: scale(1.4);
    }

    &--3 {
      top: 39vh;
      left: 122px;
    }

    &--4 {
      top: 87vh;
      left: 1201px;
    }

    &--5 {
      top: 20vh;
      left: 570px;
    }

    &--6 {
      top: 57vh;
      left: -14px;
    }

    &--7 {
      top: 18vh;
      left: 1318px;
      display: block;
    }

    &--8 {
      top: 12vh;
      left: 694px;
      display: block;
      transform: scale(0.9);
    }

    &--9 {
      top: 79vh;
      left: 558px;
    }

    &--10 {
      top: 39vh;
      left: 1417px;
    }

    &--11 {
      top: 92vh;
      left: 295px;
    }
  }
}