*, *::after, *::before {
  box-sizing: border-box;
}

.wrapper {
  max-width: 1440px;
  width: 100%;
  margin: 0 auto;
  box-sizing: border-box;
}

.button {
  width: 100%;
  max-width: 241px;
  height: 43px;
  background-color: #ffffff;
  border-radius: 32px;
  font-weight: bold;
  font-size: 10px;
  line-height: 43px;
  border: none;
  box-shadow: none;
  box-sizing: border-box;
  outline: none;
  display: inline-flex;
  align-items: center;
  justify-content: center;
}

button,
input {
  -webkit-appearance: none;
  box-shadow: none;
}

.label,
.input {
  font-weight: 500;
  font-size: 14px;
}

.input {
  width: 90%;
  outline: none;
  border: none;
  background-color: transparent;
  border-bottom: 1px solid #222222;
  height: 43px;
  box-sizing: border-box;
  margin-bottom: 10px;
  border-radius: 0;
  padding-top: 10px;
}

.label {
  position: absolute;
  width: 100%;
  left: 0;
  top: 16px;
  transition: 0.2s all ease-out;
}

.label--error {
  color: red;
}

.label--active,
.input:focus + label,
.input:valid + label {
  top: 0;
  font-size: 10px;
  border-radius: 0;
}

.form-item {
  position: relative;
  padding-top: 5px;
}

@media (min-width: 768px) {
  .button {
    font-size: 12px;
    max-width: 329px;
    height: 52px;
  }
}

