.body {
  font-family: 'Montserrat', sans-serif;
  font-weight: 400;
  font-style: normal;
  margin: 0 auto;
  font-size: 12px;
  color: #222222;
  line-height: 20px;
  min-width: 320px;
  /*  max-width: 1440px;*/
  box-sizing: border-box;
  overflow-x: hidden;
}

