    font-size: 14px;
  }
}

.social {
  display: flex;
}

.social a {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 23px;
  height: 23px;
  border-radius: 50%;
  background-color: #222222;
  text-align: center;
}

.social--active {
  display: flex;
}

.social a:not(:last-of-type) {
  margin-right: 12px;
}

