  font-size: 14px;
  text-transform: uppercase;
  text-decoration: none;
  color: #222222;
  line-height: 45px;
  position: relative;
  z-index: 10;
  cursor: pointer;
}

.header__main-nav {
  width: 100%;
  display: flex;
  position: fixed;
  flex-direction: column;
  justify-content: space-between;
  box-sizing: border-box;
  top: 0;
  height: 100%;
  padding: 41px 20px 0 20px;
  left: 0;
  z-index: 9;
  transform: scaleY(0);
  background-color: #ffffff;
  transform-origin: top;
  transition: transform 0.5s cubic-bezier(0.28, 0, 0.18, 1) 0.5s;
  overflow-y: auto;
}

.header__main-nav--active {
  transform-origin: top;
  transform: scaleY(1);
}

.header__main-nav--active .header__nav-elements,
.header__main-nav--active .header__contact-container {
  opacity: 1;
  transition: opacity 1.5s cubic-bezier(0.28, 0, 0.18, 1) 0.5s;
}

.header__contact-container,
.header__nav-elements {
  transition: opacity 0.1s cubic-bezier(0.28, 0, 0.18, 1) 0.3s;
  opacity: 0;
  padding: 0;
  list-style: none;
}

.header__nav-elements a:hover {
  border-bottom: 1px solid #222222;
}

a.header__tel {
  font-size: 12px;
}

.header__logo {
  display: flex;
  align-items: center;
  margin-left: 2px;
  z-index: 10;
}

.header__burger {
  cursor: pointer;
  position: relative;
  width: 30px;
  height: 30px;
  z-index: 10;
}

.header__burger-ico {
  position: absolute;
  top: 14px;
  display: block;
  width: 26px;
  height: 1px;
  background-color: #222222;
  transition: all .6s ease;
}

.header__burger-ico::before {
  content: "";
  position: absolute;
  top: -5px;
  display: block;
  width: 26px;
  height: 1px;
  background-color: #222222;
  transition: 0.6s cubic-bezier(0.47, -0.77, 0.6, 1.8);
}

.header__burger-ico::after {
  content: "";
  position: absolute;
  top: 5px;
  display: block;
  width: 26px;
  height: 1px;
  background-color: #222222;
  transition: 0.6s cubic-bezier(0.47, -0.77, 0.6, 1.8);
}

.header__burger-ico--active::after, .header__burger-ico--active::before {
  top: 0;
}

